<template>
	<div class="slider" ref="slider">
		<div class="slider-group" ref="sliderGroup">
			<slot></slot>
		</div>
		<div class="dots">
			<span class="dot" v-for="(item, index) in dots" :class="{active: currentPageIndex == index}" :key="index"></span>
		</div>
	</div>
</template>

<script type="text/ecmascript-6">
	import BScroll from 'better-scroll'
	import {addClass} from 'common/js/dom.js'

	export default {
		props: {
			loop: {
				type: Boolean,
				default: true
			},
			autoplay: {
				type: Boolean,
				default: true
			},
			interval: {
				type: Number,
				default: 4000
			}
		},
		mounted () {
			setTimeout(() => {
				this._setSliderWidth()
				this._initDots()
				this._initSlider()
				if (this.autoplay) {
					this._play()
				}
			}, 20)

			window.addEventListener('resize', () => {
				if (!this.slider) {
					return
				}
				this._setSliderWidth(true)
				this.slider.refresh()
			})
		},
		data () {
			return {
				dots: [],
				currentPageIndex: 0
			}
		},
		methods: {
			_setSliderWidth (resize) {
				this.children = this.$refs.sliderGroup.children
				let width = 0
				let sliderWidth = this.$refs.slider.clientWidth
				for (var i = 0; i < this.children.length; i++) {
					let child = this.children[i]
					addClass(child, 'slider-item')
					child.style.width = sliderWidth + 'px'
					width += sliderWidth
					console.log('loop' + width)
				}
				if (this.loop && !resize) {
					width += 2 * sliderWidth
				}
				this.$refs.sliderGroup.style.width = width + 'px'
			},
			_initSlider () {
				if (!this.slider) {
					this.slider = new BScroll(this.$refs.slider, {
						scrollX: true,
						scrollY: false,
						momentum: false,
						snap: {
							loop: this.loop,
							threshold: 0.3,
							speed: 400
						}
						// <a>标签click事件是默认行为，所以不需要监听click事件
					})
				}

				this.slider.on('scrollEnd', this._onScrollEnd)

				this.slider.on('touched', () => {
					if (this.autoplay) {
						this._play()
					}
				})

				this.slider.on('beforeScrollStart', () => {
					if (this.autoplay) {
						clearTimeout(this.timer)
					}
				})
			},
			_onScrollEnd () {
				let pageIndex = this.slider.getCurrentPage().pageX
				this.currentPageIndex = pageIndex
				if (this.autoplay) {
					this._play()
				}
			},
			_initDots () {
				this.dots = new Array(this.children.length)
			},
			_play () {
				clearTimeout(this.timer)
				this.timer = setTimeout(() => {
					this.slider.next()
				}, this.interval)
			}
		}
	}
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import '../common/stylus/variable'

.slider
	min-height 1px
	position relative
	.slider-group
		overflow hidden
		white-space nowrap
		position relative
		.slider-item
			float left
			overflow hidden
			box-sizing border-box
			text-align center
			a
				display block
				width 100%
				overflow hidden
				text-decoration none
			img
				display block
				width 100%
	.dots
		position absolute
		right 0
		left 0
		bottom 12px
		text-align center
		font-size 0
		.dot
			display inline-block
			margin 0 4px
			width 8px
			height 8px
			border-radius 50%
			background $color-text-l
			&.active
				width 20px
				border-radius 5px
				background $color-text-ll
</style>



